                      <!--</span>-->
        <!--</v-btn>-->

        <v-btn flat @click="goHome">BliFace</v-btn>
        <v-btn flat @click="gitHub">gitHub</v-btn>
        <v-btn flat @click="ssCode">SourceCode</v-btn>
        <v-btn flat class="hidden-xs-only" @click="goSSCoin">SS Coin</v-btn>
        <v-btn flat class="hidden-xs-only" @click="ssWhiteBook">WhiteBook</v-btn>
        <v-btn flat class="hidden-xs-only" @click="goUploadFile">Upload</v-btn>

      </v-toolbar-items>

      <v-spacer></v-spacer>
      <v-menu bottom offset-y>
        <template v-slot:activator="{ on }">
          <v-btn
            icon
            v-on="on"
            color="while"
          >
            <v-icon color="blue-grey">more_vert</v-icon>
          </v-btn>
        </template>
        <v-list>
          <v-list-tile @click="gitHub">GitHub</v-list-tile>
          <v-list-tile @click="goUploadFile">Upload Video</v-list-tile>
          <v-list-tile @click="goSSCoin">SS Coin</v-list-tile>
          <v-list-tile @click="ssCode">SourceCode</v-list-tile>
          <v-list-tile @click="ssWhiteBook">WhiteBook</v-list-tile>
          <v-list-tile @click="goManage">Manage</v-list-tile>
        </v-list>
      </v-menu>
    </v-toolbar>

    <!--<v-content class="pa-0 white" style="background-image: url(../static/image/nature-3054797_1920.jpg)">-->
