</template>

<script>
import HelloWorld from './components/HelloWorld'
import Detail from './components/Detail'
export default {
  name: 'App',
  components: {Detail, HelloWorld},
  mounted () {
  },
  methods: {
    goHome () {
      let url = 'https://babyhard.com'

      if (window.location.protocol === 'http:') {
        url = 'http://babyhard.com'
      }
      window.open(url)

      // this.$router.push({path: '/'})
    },
    goUploadFile () {
      // this.$router.push({path: '/Detail', query: {hash: videoHash}})

      let toPage = '/uploadFile'
      // 同一页面不跳转
      if (window.location.href.indexOf(toPage) > 0) {
        return
      }

      if (window.location.protocol === 'https:') {
        alert('upload video need http Protocol\nplease use http://babyhard.com')
        return
      }

      // 上传界面在https协议下会报错 强制修改为http协议中进行 （需在服务器端改善)
      this.$router.push({path: toPage})

      console.log('go: ', toPage)
    },
    goManage () {
      let toPage = '/Manage'
      this.$router.push({path: toPage})
    },
    goSSCoin () {
      let toPage = '/coinMarket'
      this.$router.push({path: toPage})
      // // 同一页面不跳转
      // if (window.location.href.indexOf(toPage) > 0) {
      //   return
      // }
      //
      // let routeData = this.$router.resolve({
      //   path: toPage
      // })
      //
      // window.open(routeData.href) // 新窗口打开  ,'_blank'
      console.log('go: ', toPage)
    },
    ssCode () {
      let url = 'https://github.com/babyhard/video/blob/master/SS%20Source%20Code.sol'
      window.open(url)
    },
    ssWhiteBook () {
      let url = 'https://github.com/babyhard/video/blob/master/ss%20whitebook.pdf'
      window.open(url)
    },
    gitHub () {
      let url = 'https://github.com/babyhard/video'
      window.open(url)
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



